    margin-right:.5em;\n}\n.purple-top .login-icon::before{\n    content:'';\n    display:inline-block;\n    background:url('login-white.svg') center no-repeat;\n    background-size:contain;\n    height:1.5em;\n    width:1.5em;\n    vertical-align: middle;\n    margin-right:.5em;\n}\n.purple-top .vr{\n    background:url('line-white.svg') center no-repeat;\n}\n/* \nSearch Bar Expand\n*/\n.searchbox{\n    position:relative;\n    min-width:30px;\n    width:0%;\n    height:30px;\n    float:right;\n    overflow:hidden;\n    transition: width 0.3s;\n}\n.searchbox-input{\n    top:0;\n    right:0;\n    border:0;\n    outline:0;\n    background:transparent;\n    width:100%;\n    height:30px;\n    margin:0;\n    padding:0px 55px 0px 20px;\n    font-size:1em;\n    color:#dcddd8;\n}\n.purple-top .searchbox-input{\n    color:#fff\n}\n.searchbox-input::-webkit-input-placeholder {\n    color: #dcddd8;\n}\n.searchbox-input:-moz-placeholder {\n    color: #dcddd8;\n}\n.searchbox-input::-moz-placeholder {\n    color: #dcddd8;\n}\n.searchbox-input:-ms-input-placeholder {\n    color: #dcddd8;\n}\n.searchbox-icon,\n.searchbox-submit{\n    width:30px;\n    height:30px;\n    display:block;\n    position:absolute;\n    top:0;\n    font-family:verdana;\n    font-size:22px;\n    right:0;\n    padding:0;\n    margin:0;\n    border:0;\n    outline:0;\n    line-height:30px;\n    text-align:center;\n    cursor:pointer;\n    color:#dcddd8;\n    background:transparent;\n}\n.searchbox-open{\n    width:50%;\n}\n.byline{\n  position:absolute;\n  top:150px;\n  left:30%;\n  text-align:center;\n  font-size:18px;\n}\n.byline a{\n  text-decoration:none;\n  color: #dcddd8;\n}\n.top-search-visible{\n    width:80%;\n    margin:0 auto;\n    display:block;\n    background-color:red;\n}\n/*# sourceMappingURL=data:application/json;base64, */"